import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import styled from 'styled-components';

@inject("ManageFile")
@observer
class FaceTypeContainer extends Component {
  render(){
    return(
      <>
        {this.props.ManageFile.faceType != "" &&
         <FaceType>
            <p style={{display: 'inline-block', fontSize: '24px', color: 'blue', fontWeight: 'bold'}}> {this.props.ManageFile.faceType}</p>
          .
        </FaceType> }
        {this.props.ManageFile.faceType == "" &&
          <>
            <div style={{ color: "white", cursor: "default"}}>
               <p fontWeight={"bold"} style={{ cursor: "default", color: "blue", display: "inline-block", fontWeight: "bold" }}></p>
               .
            </div>
            <p>. . .</p>
            <p fontWeight={"bold"} fontSize={15} style={{cursor: "default",}}> 1 .</p>
          </>
        }
      </>
    )
  }
}

export default FaceTypeContainer;

const FaceType = styled.p`
  font-size: 24px;
  cursor: default;
  ${'' /* font-weight: bold; */}
  ${'' /* color: 'green'; */}
`
